<template>
    <div class="login_bg">
        <Modal v-model="loginModal" :mask="false" :closable="false" width="360">
            <p slot="header" class="loginModalTop">
                <img class="logoImg" src="../../assets/images/login_logo.png" />
            </p>
            <div style="text-align:center">
                <Row class="p10">
                    <Col class="p1" span="24">
                        <Input class="inputWidth" v-model="loginForm.username" prefix="md-person" size="large" placeholder="请输入用户名" /> <!--   -->
                    </Col>
                </Row>
                <Row class="p10">
                  <Col class="p1" span="24">
                    <Input class="inputWidth" type="password" v-model="loginForm.password" prefix="md-key" size="large" placeholder="请输入密码" />
                  </Col>
                </Row>
                <Row class="p10">
                    <Col class="p1" span="16">
                        <Input class="inputWidth" v-model="loginForm.imgCode" prefix="logo-snapchat" size="large" placeholder="请输入验证码" />  <!-- v-on:input="inputCaptcha" -->
                    </Col>
                    <Col class="p1" span="8">
                        <span v-on:click="getNewCode" ><img class="codeImgStyle" :src="codeImg" /></span>
                    </Col>
                </Row>
            </div>
            <div slot="footer">
                <Button type="primary" size="large" long v-on:click="userLogin" >登录</Button>
            </div>
        </Modal>
    </div>
</template>

<script>
export default {
  name: 'login',
    data () {
        return {
            loginModal: true,
            getCodeBtnText: '获取验证码',
            getCodeDisabled: false,
            seconds: 60,
            code:'',
            codeImg:'',
            loginForm:{
                username:'',
                imgName:'',
                imgCode:'',
                password:''
            }
        }
    },
    created: function(){
        this.getNewCode()
    },
    methods:{
        getNewCode: function(e){
            let imgName = this.loginForm.imgName
            //获取图片验证码
            this.$axios.post('http://localhost:8099/api/getImgCode', this.$qs.stringify({
                //此处需要qs否则后台接收不到参数
                imgName: imgName
            }))
            .then(res => {
                if (res.status === 200) {
                    // console.log("200",res)
                    this.codeImg = 'http://localhost:8099/' + res.data.filePath+'.png';
                    this.loginForm.imgName = res.data.filePath;
                } else {
                    console.log("!=200",res)
                }
            })
            .catch(res => {
                console.log(res)
            })
        },
        userLogin:function(){
          var loginForm = this.loginForm;
          this.$axios.post('http://localhost:8099/login',
          this.$qs.stringify(loginForm))
            .then(res => {
              if (res.status === 200) {
                // console.log("200",res)
                if(res.data.state){
                  sessionStorage.setItem("userInfo",JSON.stringify(res.data.userInfo));
                  let redirect = decodeURIComponent(this.$route.query.redirect || '/');
                  this.$router.push({//你需要接受路由的参数再跳转
                    path: redirect
                  });
                }else{
                    this.$Message.info(res.data.msg);
                }
              } else {
                console.log("!=200",res)
              }
            })
            .catch(res => {
              console.log(res)
            })


            // localStorage.setItem("token", "sbjkadfhkasdfkasdhkfja.asdasdfahjgfuiwqehrbjkvb.gfhjiqwnerhjsdjkwefjkqweu")
            // let redirect = decodeURIComponent(this.$route.query.redirect || '/');
            // this.$router.push({//你需要接受路由的参数再跳转
            //   path: redirect
            // });
        }
    }
}
</script>

<style scoped>
.login_bg{
    width: 100vw;
    height: 100vh;
    background-image: url('../../assets/images/login_bg.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
}
.inputWidth{
    width: 100%;
}
.p1{
    padding: 1px;
}
.p10{
    padding: 10px;
}
.codeImgStyle{
    width: 100px;
    height: 35px;
    background-color: Gainsboro;
    border: 1px solid black;
    border-radius: 5px;
}
.loginModalTop{
}
.logoImg{
    position: absolute;
    top: -30px;
    left:50%;
    margin-left:-80px;
    width: 160px;
    border: 0px;
}
</style>



